import * as types from './actionTypes';
import {beginAjaxCall} from './ajaxStatusActions';
import * as apiData from '../constants';

export function loadMoviesSuccess(movies) {
  return {type: types.LOAD_MOVIES_SUCCESS, movies};
}

export function loadMovies(movie) {
  return function (dispatch) {
    dispatch(beginAjaxCall());
    return fetch(`${apiData.baseUrl}/movie/${movie}?&api_key=${apiData.apiKey}`)
      .then(response => {
        response.json().then(data => ({
            data: data,
            status: response.status
          })
        ).then(res => {
          setTimeout(() => {
            dispatch(loadMoviesSuccess(res));
          }, apiData.delay);

        });
      }).catch(error => {
        throw(error);
      });
  };
}



