import React from 'react';
import { Navbar, Nav, NavItem, NavDropdown, MenuItem } from 'react-bootstrap';
import {Link} from 'react-router-dom'

class About extends React.Component {

    render() {
        return (
            <Navbar inverse fluid>
                <Navbar.Header>
                    <Navbar.Brand>
                        Movies
                    </Navbar.Brand>
                </Navbar.Header>
                <Nav>
                    <NavItem eventKey={1}>
                      <Link to="/">
                        Home
                      </Link>
                    </NavItem>
                    <NavItem eventKey={2}>
                      <Link to="/movie">
                        Search
                      </Link>
                    </NavItem>
                    <NavDropdown eventKey={3} title="Lists" id="basic-nav-dropdown">
                        <MenuItem eventKey={3.1}>
                          <Link to="/list/viewed">
                            Viewed
                          </Link>
                        </MenuItem>
                        <MenuItem eventKey={3.2}>
                          <Link to="/list/want">
                            Will See
                          </Link>
                        </MenuItem>
                        <MenuItem eventKey={3.3}>
                          <Link to="/list/could">
                            Could See
                          </Link>
                        </MenuItem>
                        <MenuItem divider />
                        <MenuItem eventKey={3.4}>
                          <Link to="/list/downloaded">
                            Downloaded
                          </Link>
                        </MenuItem>
                    </NavDropdown>
                </Nav>
            </Navbar>
        );
    }
}

export default About;



