import * as types from './actionTypes';
import * as apiData from '../constants';

export function loadSimilarSuccess(similar) {
  return {type: types.LOAD_SIMILAR_SUCCESS, similar};
}

export function loadSimilar(movie) {
  return function (dispatch) {
    return fetch(`${apiData.baseUrl}/movie/${movie}/similar?&api_key=${apiData.apiKey}`)
      .then(response => {
        response.json().then(data => ({
            data: data,
            status: response.status
          })
        ).then(res => {
          setTimeout(() => {
            dispatch(loadSimilarSuccess(res));
          }, apiData.delay);
        });
      }).catch(error => {
        throw(error);
      });
  };
}



