import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import {InputGroup, FormControl} from 'react-bootstrap';

import {loadMovies} from '../actions/movieActions';
import {searchMovies} from '../actions/searchActions';
import {loadSimilar} from '../actions/similarActions';
import search from '../../public/images/search.png';

let createHandlers = function(dispatch) {
  return {
    click: function(node) {
      dispatch(loadMovies(node));
      dispatch(loadSimilar(node));
    },
    search: function (node) {
      dispatch(searchMovies(node))
    }
  };
};

class Search extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      searchValue: '',
      showSelector: false
    };

    this.find = this.find.bind(this);
    this.setNewMovie = this.setNewMovie.bind(this);
    this.clearForm = this.clearForm.bind(this);
    this.handlers = createHandlers(this.props.dispatch);
  }

  find(event) {
    this.setState({searchValue: event.target.value});
    this.setState({showSelector: true});

    if(event.target.value !== ''){
      this.props.searchFunction(event.target.value);
    }else{
      this.clearForm();
    }
  }

  setNewMovie(id){
    this.handlers.click(id);
    this.clearForm();
  }

  clearForm(){
    this.setState({searchValue: ''});
    this.setState({showSelector: false});
  }

  render() {
    return (
      <div>
        <InputGroup>
          <InputGroup.Addon>
            <img src={search} alt="search"/>
          </InputGroup.Addon>
          <FormControl type="text" placeholder="Search" value={this.state.searchValue} onChange={this.find}/>
        </InputGroup>
        <ul className="search-results">
          {this.state.showSelector && this.props.searchOptions &&
            this.props.searchOptions.map(search =>
            <li key={search.id}
                onClick={() => this.setNewMovie(search.id)}
                onBlur={() => this.clearForm}>
              {search.title} - ({search.release_date.substr(0,4)})
              </li>
          )}
        </ul>
      </div>
    );
  }
}

Search.propTypes = {
  searchFunction: PropTypes.func.isRequired
};

function mapStateToProps(state) {
  let searchResults = [];
  if(state.search.data){
    searchResults = state.search.data.results.slice(0,5)
  }
  return {
    searchOptions: searchResults
  };
}

export default connect(mapStateToProps)(Search);



