import React, {PropTypes} from 'react';
import {Panel, Col, DropdownButton, MenuItem} from 'react-bootstrap';
import Search from './Search';
import noImage from '../../public/images/No_image_available.svg'

const MovieCard = ({movie,status,list,similar,click,searchFunction,saveToStorage}) => {
  let poster = '';
  if(movie.data.poster_path){
    poster = `https://image.tmdb.org/t/p/w500${movie.data.poster_path}`
  }else{
    poster = noImage;
  }

  if(!status){
    status = 'Add to';
  }

  return (
    <Panel>
      <Col xs={12} md={8} mdPush={4} lg={7} lgPush={5}>
        <h1 className="movie-title">
          {movie.data.original_title}
          <br/>
          <small className="movie-tagline">
            {movie.data.tagline}
          </small>
        </h1>
        <DropdownButton title={status} id="bg-nested-dropdown">
          <MenuItem onClick={() => saveToStorage(movie.data.id,movie.data.original_title,poster,'viewed',list)}>
            Viewed
          </MenuItem>
          <MenuItem onClick={() => saveToStorage(movie.data.id,movie.data.original_title,poster,'want',list)}>
            Want to view
          </MenuItem>
          <MenuItem onClick={() => saveToStorage(movie.data.id,movie.data.original_title,poster,'could',list)}>
            Could view
          </MenuItem>
          <MenuItem onClick={() => saveToStorage(movie.data.id,movie.data.original_title,poster,'downloaded',list)}>
            Downloaded
          </MenuItem>
          <MenuItem divider />
          <MenuItem onClick={() => saveToStorage(movie.data.id,movie.data.original_title,poster,'remove',list)}>
            Remove from list
          </MenuItem>
        </DropdownButton>
        <br/>
        <p className="movie-description">
          {movie.data.overview}
        </p>
        <div className="meta-data">
          <Col xs={12} md={6} >
            <h1>Genres</h1>
            {movie.data.genres &&
              movie.data.genres.map(genre =>
                <div key={genre.id + Math.random()} >
                  <span>{genre.name}</span>
                  <br/>
                </div>
              )}
          </Col>

          <Col xs={12} md={6} >
            <h1>Release date</h1>
            <span>
                    {movie.data.release_date}
                  </span>
          </Col>

          <Col xs={12} md={6} >
            <h1>Votes </h1>
            <span>
                    {movie.data.vote_average} / 10
                  </span>
          </Col>

          <Col xs={12} className="no-padding">
            <h1>Similar</h1>
            {similar.map(similar =>
              <Col xs={2} onClick={() => click(similar.id)} key={similar.id}>
                {
                  similar.poster_path &&
                  <img src={`https://image.tmdb.org/t/p/w500${similar.poster_path}`}
                       className="img-responsive image-similar"
                       alt=""
                       title={similar.original_title}/>
                }
                {
                  !similar.poster_path &&
                  <img src={noImage} className="img-responsive image-similar" alt="" title={similar.original_title}/>
                }
              </Col>
            )}
          </Col>
        </div>
      </Col>

      <Col className="movie-image-container" xs={12} md={4} mdPull={8} lg={5} lgPull={7}>
        <img src={poster}
             className="img-responsive center-block"
             alt={movie.data.original_title}/>
      </Col>
      <Col xs={12} className="search-container">
        <Search searchFunction={searchFunction}/>
      </Col>
    </Panel>
  );
};

MovieCard.propTypes = {
  movie: PropTypes.object.isRequired,
  status: PropTypes.string,
  list: PropTypes.string,
  similar: PropTypes.array.isRequired,
  click: PropTypes.func.isRequired,
  searchFunction: PropTypes.func.isRequired,
  saveToStorage: PropTypes.func.isRequired
};

export default MovieCard;



