import React, { Component } from 'react';
import {Link} from 'react-router-dom'

class App extends Component {

  componentDidMount() {
    document.body.style.backgroundImage = '';
  }

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h2>Movies list web</h2>
        </div>
        <p className="App-intro">
          Here you can search for several movies and set it into lists, so you can keep track of each viewed movie,
          or those that you will love to see some day.
          <br/><br/>
          Just click <Link to="/movie">Search</Link> and start listing your movies
        </p>
      </div>
    );
  }

}

export default App;



