import React, {PropTypes} from 'react';
import {connect} from 'react-redux';

import MovieCard from './MovieCard';
import Loader from './Loader';
import {loadMovies} from '../actions/movieActions';
import {searchMovies} from '../actions/searchActions';
import {loadSimilar} from '../actions/similarActions';
import powered from '../../public/images/poweredby.svg';


let createHandlers = function(dispatch) {
  return {
    click: function(node) {
      dispatch(loadMovies(node));
      dispatch(loadSimilar(node));
    },
    search: function (node) {
      dispatch(searchMovies(node))
    }
  };
};

class Movies extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.handlers = createHandlers(this.props.dispatch);
    this.saveToStorage = this.saveToStorage.bind(this);
    this.checkInList = this.checkInList.bind(this);

    this.state = {
      movieStatus: '',
      movieList: ''
    };

  }

  saveToStorage(id,title,poster,type, old){
    let types = {
      viewed: 'Viewed',
      want: 'Want to view',
      could: 'Could view',
      downloaded: 'Downloaded',
      remove: 'Add to'
    };

    //Delete item from old list
    if(old){
      let oldItems = JSON.parse(localStorage.getItem('movies_'+old));
      let newItems = oldItems.filter(function (oldItem) {
        return oldItem.id !== id
      });
      localStorage.setItem('movies_'+old,JSON.stringify(newItems));
    }
    if(type !== 'remove'){
      let items = JSON.parse(localStorage.getItem('movies_'+type));
      if(!Array.isArray(items)){
        items = [];
      }
      items.push({
        id,
        title,
        poster
      });
      localStorage.setItem('movies_'+type,JSON.stringify(items));
    }

    this.setState({movieStatus: types[type]});
    this.setState({movieList: type});
  }

  checkInList(type,id){
    if(type && id){
      let items = JSON.parse(localStorage.getItem('movies_'+type));
      if(items){
        for(let i = 0; i < items.length; i++){
          if(items[i].id === id){
            return true;
          }
        }
      }
    }
    return false;
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.movies.data && (this.props.movies.data.id !== nextProps.movies.data.id)) {
      // Necessary to populate form when existing course is loaded directly.
      // this.setState({movies: Object.assign({}, nextProps.movies)});
      document.body.style.backgroundImage = 'url(https://image.tmdb.org/t/p/original' + this.props.movies.data.backdrop_path + ')'
      this.setState({movieStatus: ''});
      this.setState({movieList: ''});
    }
  }

  componentDidMount(){
    if(this.props.movies.data){
      document.body.style.backgroundImage = 'url(https://image.tmdb.org/t/p/original' + this.props.movies.data.backdrop_path + ')';
    }
  }

  componentDidUpdate(props,state) {
    document.body.style.backgroundImage = 'url(https://image.tmdb.org/t/p/original' + this.props.movies.data.backdrop_path + ')';
    if(state.movieStatus === '') {
      let types = ['viewed', 'want', 'could', 'downloaded'];
      let text = ['Viewed', 'Want to view', 'Could view', 'Downloaded'];

      for (let i = 0; i < types.length; i++) {
        if (this.checkInList(types[i], this.props.movies.data.id)) {
          this.setState({movieStatus: text[i]});
          this.setState({movieList: types[i]});
          i = types.length;
        }
      }
    }
  }

  render() {
    return (
      <div className="container">
        {this.props.loading && <Loader />}
        {
          this.props.movies.data &&
          <MovieCard
            movie={this.props.movies}
            status={this.state.movieStatus}
            list={this.state.movieList}
            similar={this.props.similar}
            click={this.handlers.click}
            searchFunction={this.handlers.search}
            saveToStorage={this.saveToStorage}
          />
        }
        <img src={powered} className="App-logo" alt="logo" />
      </div>
    );
  }
}

Movies.propTypes = {
  movies: PropTypes.object.isRequired,
  similar: PropTypes.array.isRequired,
  loading: PropTypes.bool.isRequired
};

function mapStateToProps(state) {
  let similar = [];
  if(state.similar.data){
    similar = state.similar.data.results.slice(0,6);
  }
  return {
    movies: state.movies,
    similar: similar,
    loading: state.ajaxCallsInProgress > 0
  };
}


export default connect(mapStateToProps)(Movies);



