import React from 'react';
import {Panel, Col} from 'react-bootstrap'

class List extends React.Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      items: [],
      type:''
    }
  }

  componentDidMount() {
    document.body.style.backgroundImage = '';
    this.setState({items: JSON.parse(localStorage.getItem('movies_'+this.props.match.params.id))});
    this.setState({type: this.props.match.params.id});
  }

  componentDidUpdate(prevProps, prevState){
    if(prevState.type !== this.props.match.params.id){
      this.setState({type: this.props.match.params.id});
      this.setState({items: JSON.parse(localStorage.getItem('movies_'+this.props.match.params.id))});
    }
  }

  render() {
    return (
      <div className="container">
        <Panel className="movies-list">
          <Col xs={12}>
            <h2>Movies in list: {this.state.items.length}</h2>

            {this.state.items.map(item =>
              <Col xs={2} key={item.id} className="text-center card-list">
                <img src={item.poster}
                     className="img-responsive center-block"
                     alt={item.title}/>
                <p>{item.title}</p>
              </Col>
            )}
          </Col>
        </Panel>
      </div>
    );
  }
}

export default List;



