import * as types from './actionTypes';
import * as apiData from '../constants';

export function searchMoviesSuccess(search) {
  return {type: types.SEARCH_MOVIES_SUCCESS, search};
}

export function searchMovies(text) {
  return function (dispatch) {
    return fetch(`${apiData.baseUrl}/search/movie?query=${text}&api_key=${apiData.apiKey}`)
      .then(response => {
        response.json().then(data => ({
            data: data,
            status: response.status
          })
        ).then(res => {
          dispatch(searchMoviesSuccess(res));
        });
      }).catch(error => {
        throw(error);
      });
  };
}



